@mixin optional-at-root($sel) {
  @at-root #{if(not &, $sel, selector-append(&, $sel))} {
    @content;
  }
}

@mixin placeholder {
  @include optional-at-root('::-webkit-input-placeholder') {
    @content;
  }

  @include optional-at-root(':-moz-placeholder') {
    @content;
  }

  @include optional-at-root('::-moz-placeholder') {
    @content;
  }

  @include optional-at-root(':-ms-input-placeholder') {
    @content;
  }
}

@include placeholder {
  color: $greyLight;
}

// Global styling for input forms that need to line up with submit button
// https://philipwalton.github.io/solved-by-flexbox/demos/input-add-ons/

.admin--flexbox-form,
.form-group {
	display: flex;
	align-items:center;
	justify-content: center;
	position:relative;
	overflow:hidden;
	margin:0 0 5px 0;
	input, textarea, > div {
		// > div is here so that immediate child elements of this admin--flexbox-form div still inherit styling
		flex-grow:1;
	}
	.btn {
		margin: 0 30px 0 0;
		padding-top: 10px;
		padding-bottom: 10px;
	}
}

// Default input styling for admin

input:not([type="submit"]):not([type="radio"]):not([type="checkbox"]),
textarea,
select {
	width: 100%;
	padding:10px;
	margin:0 5px 0 0;
	border: 1px solid $greyLight;
	border-radius:4px;
	background-image: none;
	box-shadow: 0 0 0;
	background:none;
    background-color: white;
	transition:all .35s;
	color:$grey;
	&:focus, &.changed {
		color:#444;
		background:white;
		border-color:$grey;
		outline:none;
		box-shadow:none;
		&:invalid {
			border-color:$brandLighter;
		}
	}
	&.changed {
		border-color:$blueLight;
		&:invalid {
			border-color:$brandLighter;
		}
	}
}

.input-zip {
	max-width:150px;
}

textarea {
	// additional height spec for textarea inputs
	height:250px;
}

input[type=text] {
	.admin--editor-page-title & {
		margin:10px 0;
		color:$black;
		font-weight:bold;
		font-size: 13px;
	}
}

input[type=text].form-control, 
input[type=file].form-control, 
select.form-control { 
	height: 36px; 
	line-height: 36px; 
	width: 100%; 
}

input[type="submit"],
.submit {
	@extend .btn;
	border:none;
	outline:none;
	color:white;
	background:$brand;
	border-radius:4px;
	transition:all .35s;
	padding:10px 12px;
	margin:0;
	max-width:150px;
	&:hover {
		background:#770e16;
	}
}