.u-serif {
    font-family: $serif;
}

.u-inlineBlock {
	display:inline-block;
	vertical-align: middle;
	margin:0;
}
.u-block {
    display: block;
}

.u-floatLeft {
	float:left;
}
.u-floatRight {
	float:right;
}

.u-textCenter {
	text-align: center;
}
.u-textRight {
	text-align: right;
}
.u-textLeft {
	text-align: left;
}

.u-uppercase {
	text-transform: uppercase;
}
.u-standardCase {
	text-transform: none;
}

.u-positionAbsolute {
	position: absolute;
}
.u-positionRelative {
	position: relative;
}
.u-positionStatic {
    position: static;
}

.u-fullHeight {
	height: 100%;
	height:100vh;
}
.u-fullMinHeight {
	min-height: 100%;
	min-height: 100vh;
}
.u-halfHeight {
	height:400px;
	height:50vh;
}
.u-fullWidth {
	width:100%;
}
.u-maxWidth320 {
    display: block;
    max-width: 320px;
    margin-left: auto;
    margin-right: auto;
}
.u-maxWidth360 {
    display: block;
    max-width: 360px;
    margin-left: auto;
    margin-right: auto;
}

.u-flexBox {
	display: flex;
	align-items:center;
}
.u-flexBox-stretch {
	align-items:stretch;
}
.u-flexBox-column {
	flex-direction: column;
}
.u-flexChild {
	flex: 1;
	& + & {
		//padding-left:25px;
	}
	> *:first-child {
		margin-top: 0;
	}
	> *:last-child {
		margin-bottom:0;
	}
}

.u-bold {
	font-weight: bold;
}

.u-overflowHidden {
	overflow: hidden;
}

.u-linkReset {
	text-decoration:none;
	transition:color .35s;
}

.u-hasBgImage {
	background-size:cover;
	background-repeat:no-repeat;
	background-position: center top;
	color:white;
	//background-attachment: fixed;
}

.u-verticalAlign {
	top:50%;
	transform:translate(0,-50%);
	position: relative;
}

.u-resetList {
	margin:0;
	padding:0;
	list-style-type:none;
}

.u-showOnHover {
	opacity:0;
	transition:all .35s;
}

.u-showOnHover.isShown {
	opacity:1;
}

.u-hidden {
	display: none;
}

.u-rotate {
	animation: rotate 1s infinite linear;
}

.u-textXs {
	font-size: 50%;
}
.u-textSm {
	font-size: 75%;
}
.u-textMd {
	font-size: 100%;
}
.u-textLg {
	font-size: 125%;
}
.u-textXl {
	font-size: 150%;
}
.u-textXxl {
	font-size: 200%;
}

.u-textBlack {
	color: black;
}

%hasDarkBg {
	color:white;
	input:not([type="submit"]) {
		background-color:white;
	}
}
.u-blueLighterBg {
	background-color:$blueLighter;
}
.u-greyLightBg {
	background-color:$greyLighter;
}
.u-greyDarkBg {
	background-color:$greyDark;
	@extend %hasDarkBg;
}
.u-redLightBg {
	background-color:$brandLighter;
}
.u-blueDarkBg {
	background-color:$blueDark;
	@extend %hasDarkBg;
}
.u-blueLightBg {
	background-color:$blueLight;
	@extend %hasDarkBg;
}

.u-textGreyMedium {
    color: $greyMedium;
}

.u-roundedCorners {
	border-radius:4px;
}

.u-boxShadow {
	box-shadow:0 0 5px #ccc;
}