.mediaBoxHalf-preview,.mediaBoxFull-preview {
    background-size: cover !important;
    background-repeat: no-repeat !important;
    background-position: center !important;
    background-color: #666;
    height: 300px;
    overflow: hidden;
    position: relative;
	iframe[src*="youtube"],
    video {
        position: absolute;
        top: -75px;
        right: 0;
        bottom: 0;
        left: 50%;
        margin-left: -400px;
        z-index: 0;
    }
}

.mediaBox-thumbs.row {
	margin: 5px -5px 0;
	&:not(.mediaBox-video) {
		max-height: 140px;
		overflow: auto;
		&::-webkit-scrollbar {
			width: 12px;
		}
		&::-webkit-scrollbar-track {
			background-color: #f0f0f0;
			border-left: none;
		}
		&::-webkit-scrollbar-thumb {
			background-color: #ccc;
		}
		&::-webkit-scrollbar-thumb:hover {
			background-color: #aaa;
		}
	}
	>div {
		position: relative;
		float: left;
		width: 15%;
		height: 46px;
		margin: 5px;
		cursor: pointer;
		&.mediaBox-imgThumb {
			>div { position: absolute; top: 0; right: 0; bottom: 0; left: 0; background-size: cover !important; background-repeat: no-repeat !important; background-position: center !important; z-index: 2; }
			&:after {
				content: "\f110";

				display: inline-block;
				font: normal normal normal 14px/1 FontAwesome;
				font-size: inherit;
				text-rendering: auto;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;

				font-size: 2em;

				-webkit-animation: fa-spin 2s infinite linear;
				animation: fa-spin 2s infinite linear;

				position: absolute;
				left: 50%;
				top: 50%;
				margin-top: -0.5em;
				margin-left: -0.5em;
				z-index: 1;
			}		
		}
		
		&.mediaBox-vidThumb {
			white-space: nowrap;
			line-height: 46px;
			border: 1px solid #ccc;
			padding: 0 10px;
			>div { overflow: hidden; text-overflow: ellipsis; }
		}
		&:hover { box-shadow: 0 0 2px 0px $blue; }
		&.selected { box-shadow: 0 0 2px 1px $blue; }
	}
}