
.modal-editor-inner.slide-editor {
	display: block;
}

.slide-preview { height: 250px; padding: 50px; margin: 20px 0; background-size: cover !important; background-repeat: no-repeat !important; background-position: center !important; background-color: #666; border: 1px solid #666; color: $white; overflow: hidden; position: relative; }
.slide-preview {
	iframe, video { position: absolute; top: -100px; right: 0; bottom: 0; left: 0; z-index: 0; }
}
.slide-preview .slide-content { position: absolute; display: inline-block; z-index: 1; font-size: 30px; top: 50%; right: 50px; left: 50px; transform: translate(0,-50%); }
.slide-preview .slide-content .slide-preview-title { font-size: 100%; text-transform: uppercase; font-family: $serif; }
.slide-preview .slide-content .slide-preview-subtitle { font-size: 46%; }
.slide-preview .slide-content .slide-preview-button {
	@extend .btn;
	font-size: 41.6667%;
	display: inline-block;
	background: transparent;
	cursor: pointer;
	margin-top: 15px;
	border-color: transparent;
	&:not(.solid) {
		&.black { border-color: $black; color: $black; }
		&.blue { border-color: $blue; color: $blue; }
		&.red { border-color: $brand; color: $brand; }
		&.grey { border-color: $greyMedium; color: $greyMedium; }
		&.white { border-color: $white; color: $white; }
	}
	&.solid {
		&.black { border-color: $black; color: $white; background-color: $black; }
		&.blue { border-color: $blue; color: $white; background-color: $blue; }
		&.red { border-color: $brand; color: $white; background-color: $brand; }
		&.grey { border-color: $greyMedium; color: $white; background-color: $greyMedium; }
		&.white { border-color: $white; color: $black; background-color: $white; }
	}
}

#slideSubtitle { white-space: nowrap; height: 90px; }

.slide-align {
	background: $greyMedium;
	color: #fff;
	height: 36px;
	margin-right: 8px;
	position: relative;
	cursor: pointer;

	-webkit-box-flex: 1;
	-moz-box-flex: 1;
	width: 100%;
	-webkit-flex: 1;
	-ms-flex: 1;
	flex: 1;
}
.slide-align:last-child { margin-right: 0; }
.slide-align:hover { background: $grey; }
.slide-align.selected { background: $brand; }
.slide-align:before, .slide-align span, .slide-align:after { position: absolute; height: 2px; background: #fff; }
.slide-align:before { content: " "; width: 43%; top: 8px; }
.slide-align span { width: 63%; top: 50%; margin-top: -1px; }
.slide-align:after { content: " "; width: 23%; bottom: 8px; }

.slide-align.left:before, .slide-align.left span, .slide-align.left:after { left: 5px; }

.slide-align.center:before, .slide-align.center span, .slide-align.center:after { left: 50%; }
.slide-align.center:before { margin-left: -21.5%; }
.slide-align.center span { margin-left: -31.5%; }
.slide-align.center:after { margin-left: -11.5%; }

.slide-align.right:before, .slide-align.right span, .slide-align.right:after { right: 5px; }

.slider-thumbs.row {
	margin: 5px -5px 0;
	&:not(#slideBackgroundCustom-video-thumbs):not(#slideBackgroundStock-video) {
		max-height: 140px;
		overflow: auto;
		&::-webkit-scrollbar {
			width: 12px;
		}
		&::-webkit-scrollbar-track {
			background-color: #f0f0f0;
			border-left: none;
		}
		&::-webkit-scrollbar-thumb {
			background-color: #ccc;
		}
		&::-webkit-scrollbar-thumb:hover {
			background-color: #aaa;
		}
	}
	>div {
		position: relative;
		float: left;
		width: 31%;
		height: 46px;
		margin: 5px;
		cursor: pointer;
		&.slider-imgThumb {
			>div { position: absolute; top: 0; right: 0; bottom: 0; left: 0; background-size: cover !important; background-repeat: no-repeat !important; background-position: center !important; z-index: 2; }
			&:after {
				content: "\f110";

				display: inline-block;
				font: normal normal normal 14px/1 FontAwesome;
				font-size: inherit;
				text-rendering: auto;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;

				font-size: 2em;

				-webkit-animation: fa-spin 2s infinite linear;
				animation: fa-spin 2s infinite linear;

				position: absolute;
				left: 50%;
				top: 50%;
				margin-top: -0.5em;
				margin-left: -0.5em;
				z-index: 1;
			}		
		}
		
		&.slider-vidThumb {
			white-space: nowrap;
			line-height: 46px;
			border: 1px solid #ccc;
			padding: 0 10px;
			>div { overflow: hidden; text-overflow: ellipsis; }
		}
		&:hover { box-shadow: 0 0 2px 0px $blue; }
		&.selected { box-shadow: 0 0 2px 1px $blue; }
	}
}

.slide-button-style {
	background: transparent;
	display: inline-block;
	margin-right: 8px;
	cursor: pointer;
	-webkit-transform: box-shadow 0.3s;
	   -moz-transform: box-shadow 0.3s;
	    -ms-transform: box-shadow 0.3s;
	     -o-transform: box-shadow 0.3s;
	        transform: box-shadow 0.3s;

	padding: 0 10px;
	height: 38px;
	line-height: 36px;
	border: 2px solid $black;
	border-radius: 5px;
	color: $black;
}
.slide-button-style:not(.solid).blue { border-color: $blue; color: $blue; }
.slide-button-style:not(.solid).red { border-color: $brand; color: $brand; }
.slide-button-style:not(.solid).grey { border-color: $greyMedium; color: $greyMedium; }
.slide-button-style:not(.solid).white { border-color: $white; color: $white; background-color: #999; box-shadow: 0 0 6px 2px #999; }

.slide-button-style.solid.blue { border-color: transparent; color: $white; background-color: $blue; }
.slide-button-style.solid.red { border-color: transparent; color: $white; background-color: $brand; }
.slide-button-style.solid.grey { border-color: transparent; color: $white; background-color: $greyMedium; }
.slide-button-style.solid.white { border-color: transparent; color: $black; background-color: $white; box-shadow: 0 0 4px 0px #999; }

.slide-button-style:last-child { margin-right: 0; }
.slide-button-style:hover { box-shadow: 0 0 2px 1px $blue !important; }
.slide-button-style.selected { box-shadow: 0 0 2px 2px $blue !important; }


/* admin section */
.admin--slideTile {
	height: 120px;
	margin-bottom: 30px;
	position: relative;
	user-select: none;
	>div {
		position: absolute;
		top: 0;
		right: 15px;
		bottom: 0;
		left: 15px;
		cursor: move;
		* {
			cursor: move !important;
		}
	}
	>span {
		position: absolute;
		top: 0;
		right: 15px;
		bottom: 0;
		left: 15px;
		line-height: 120px;
		font-size: 48px;
		text-align: center;
		color: $white;
		z-index: 3;
		opacity: 1;
		transition: opacity 0.3s;
		cursor: move;
		text-shadow: 0px 1px 2px #000;
		background: rgba(0, 0, 0, 0.5);
		&:hover {
			opacity: 0;
		}
	}
	&:hover span {
		opacity: 0;
	}
	.slide-preview {
		margin: 0;
		border: none;
		height: auto;
		video { left: 50%; margin-left: -173px; top: 50%; transform: translateY(-50%); }
		.slide-content {
			font-size: 10px;
			right: 20px;
			left: 20px;
			.slide-preview-button {
				&.black, &.blue, &.red, &.grey, &.white {
					padding: 2px 15px; margin-top: 5px;
				}
			}
		}
	}
}

.admin--slideDetails {
	.admin--tree-page {
		min-height: 71px;
		.slideDetails-img {
			background-size: cover!important;
			background-repeat: no-repeat!important;
			background-position: center!important;
			background-color: #666;

			float: left;
			width: 75px;
			height: 40px;
			margin: 0 15px;
			overflow: hidden;
			position: relative;
		
			video { position: absolute; top: 50%; left: 0; transform: translateY(-50%); }
		}
		.slideDetails-title {
			font-weight: bold;
			color: $black;
			line-height: 20px;
		}
		.slideDetails-subtitle {
			color: $blue;
			line-height: 20px;
		}
		&:after {
			content: " ";
			clear: both;
		}
	}
}