.ourLeader-preview {
    margin: 20px 0;
    background-size: cover !important;
    background-repeat: no-repeat !important;
    background-position: center !important;
    background-color: #666;
    border: 1px solid #666;
    color: white;
    overflow: hidden;
    position: relative;
	.ourLeader-preview-content {
		padding: 40px;
		text-align: center;
		background: rgba(0,0,0,.6);
		&.right {
			text-align: left;
			padding: 30px 40px 30px 60%;
			&.blue {
				background: linear-gradient(to left, rgba(0,38,85,.6) 5%,rgba(0,38,85,0) 75%);
			}
			&.black {
				background: linear-gradient(to left, rgba(0,0,0,.6) 5%,rgba(0,0,0,0) 75%);
			}
			.ourLeader-blurb {
				padding: 0;
			}
		}
		&.left {
			text-align: left;
			padding: 30px 60% 30px 40px;
			&.blue {
				background: linear-gradient(to right, rgba(0,38,85,.6) 5%,rgba(0,38,85,0) 75%);
			}
			&.black {
				background: linear-gradient(to right, rgba(0,0,0,.6) 5%,rgba(0,0,0,0) 75%);
			}
			.ourLeader-blurb {
				padding: 0;
			}
		}
		&.center {
			&.blue {
				background: rgba(0,38,85,.6);
			}
			&.black {
				background: rgba(0,0,0,.6);
			}
		}
		&.none {
			background: none;
		}
		.ourLeader-blurb {
			font-size: 13px;
			line-height: 20px;
			padding: 10px 50px;
		}
		.ourLeader-btn {
			border-color: white;
			color: white;
			background: transparent;
		}
	}
}

#ourLeader-BackgroundImgs.row { margin-left: -5px; margin-right: -5px; padding-top: 5px; }

#ourLeader-BackgroundImgs > div.ourLeader-BackgroundImg { position: relative; height: 46px; margin: 5px 0; }
#ourLeader-BackgroundImgs > div.ourLeader-BackgroundImg > div { position: absolute; top: 0; right: 5px; bottom: 0; left: 5px; background-size: cover !important; background-repeat: no-repeat !important; background-position: center !important; z-index: 2;  cursor: pointer; }
#ourLeader-BackgroundImgs > div.ourLeader-BackgroundImg:after {
	content: "\f110";

	display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

	font-size: 2em;

	-webkit-animation: fa-spin 2s infinite linear;
    animation: fa-spin 2s infinite linear;

    position: absolute;
    left: 50%;
    top: 50%;
    margin-top: -0.5em;
    margin-left: -0.5em;
    z-index: 1;
}
#ourLeader-BackgroundImgs > div.ourLeader-BackgroundImg > div:hover { box-shadow: 0 0 2px 0px $blue; }
#ourLeader-BackgroundImgs > div.ourLeader-BackgroundImg.selected > div { box-shadow: 0 0 2px 1px $blue; }


.ourLeader-alignContainer { max-width: 140px; }
.ourLeader-align {
	background: $greyMedium;
	color: #fff;
	height: 36px;
	margin-right: 8px;
	position: relative;
	cursor: pointer;

	-webkit-box-flex: 1;
	-moz-box-flex: 1;
	width: 100%;
	-webkit-flex: 1;
	-ms-flex: 1;
	flex: 1;
}
.ourLeader-align:last-child { margin-right: 0; }
.ourLeader-align:hover { background: $grey; }
.ourLeader-align.selected { background: $brand; }
.ourLeader-align:before, .ourLeader-align span, .ourLeader-align:after { position: absolute; height: 2px; background: #fff; }
.ourLeader-align:before { content: " "; width: 43%; top: 8px; }
.ourLeader-align span { width: 63%; top: 50%; margin-top: -1px; }
.ourLeader-align:after { content: " "; width: 23%; bottom: 8px; }

.ourLeader-align.left:before, .ourLeader-align.left span, .ourLeader-align.left:after { left: 5px; }

.ourLeader-align.center:before, .ourLeader-align.center span, .ourLeader-align.center:after { left: 50%; }
.ourLeader-align.center:before { margin-left: -21.5%; }
.ourLeader-align.center span { margin-left: -31.5%; }
.ourLeader-align.center:after { margin-left: -11.5%; }

.ourLeader-align.right:before, .ourLeader-align.right span, .ourLeader-align.right:after { right: 5px; }