.givingToolbar-toolbar {
    background: $brand;
    margin: 30px 0;
    display: flex;
    > div {
        flex: 1 1 auto;
        display:flex;
        justify-content: center;
        color: white;
        text-transform: uppercase;
        padding: 20px 0;
        white-space: nowrap;
        cursor: pointer;
        &:hover {
            background: $brandLight;
        }
        .fa {
            float: left;
            height: 38px;
            line-height: 38px;
            margin-right: 10px;
            font-size: 28px;
        }
    }
}

.givingToolbar-links {
    color: $greyMedium;
    > div {
        display: flex;
        padding: 10px 0;
        > * {
            display:flex;
            flex-direction: column;
            justify-content: center;
        }
        .givingToolbar-link {
            text-transform: uppercase;
            white-space: nowrap;
            width: 190px;
            > span {
                height: 38px;
                .fa {
                    float: left;
                    margin-right: 10px;
                    font-size: 28px;
                    line-height: 38px;
                    height: 38px;
                }
            }
        }
        > .fa {
            width: 110px;
            font-size: 28px;
            text-align: center;
        }
        .givingToolbar-description {
            flex: 1;
        }
    }
}