.contactInfo-toolbar {
    background: $blueDark;
    margin: 30px 0;
	padding: 10px 30px;
    display: flex;
    > div {
        flex: 1 1 auto;
        display:flex;
        justify-content: center;
		line-height: 30px;
        color: white;
        padding: 20px 0;
        white-space: nowrap;
        .fa {
            float: left;
            height: 30px;
            width: 30px;
            line-height: 30px;
            margin-right: 10px;
            font-size: 16px;
            text-align: center;
			border: 1px solid white;
			border-radius: 50%;
        }
		b {
			margin-left: 4px;
		}
    }
}

.conactInfo-emailContainer {
	input[type="text"] {
		display: inline-block;
		&:first-of-type {
			width: 40%;
		}
		&:last-of-type {
			width: 50%;
		}
	}
}