* {
	box-sizing:border-box;
}

body {
	width:100%;
	overflow-x:hidden;
}

img {
	max-width:100% !important;
	//important overrides the inline height/width added by WM4
	height:auto;
}

hr {
	border:none;
	border-top:1px solid $greyLighter;
	margin:1em auto;
	max-width: $container-lg;
}

a {
	color:$brand;
	transition:color .35s;
}

a:focus {
	outline:none;
}

a:hover {
	color:$blueDark;
}

main {
	max-width:100%;
	width:100%;
	display:block;
}

.icon {
	background-size:contain;
	background-position:center center;
	width:auto;
	height:75px;
	display:block;
	margin: 0 auto;
}

.icon.fa {
  font-size: 75px;
  color:$blueDark;
}

.col-md-4 .icon {
	height: 75px;
}

// Add padding to the bottom of the last container on a page, before the footer
// Allows the footer shield to have some breathing room

#page > div:last-of-type {
	padding-bottom: 75px;
}

@media (min-width:$screen-md-min) {
	.icon {
		height: 150px;
	}
  .icon.fa {
    font-size: 145px;
    color:$blueDark;
  }
	hr {
		margin: 2em auto;
	}
}

@media (max-width:$screen-xs-max) {
	[class*="col-md-4"] {
		margin-bottom: 2em;
	}
}


.youtube { /* continer (responsive) */
  position: relative;
  cursor: pointer;
  background: #ccc no-repeat center center / cover; 
  padding-bottom: 56.25%; /* 16:9 */
  height: 0px;
  clear:both;
}

.youtube::before { /* play bubble */
  content: '';
  background: rgba(255, 255, 255, .6);
  height: 80px;
  width: 80px;
  display:block;
  margin: -40px 0 0 -40px;
  border-radius: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 0 20px 3px rgba(51, 51, 51, .8);
  transition: all .1s ease;
  z-index:1;
}

.youtube:hover::before { /* play bubble hover */
  background: rgba(238, 238, 238, .8);
  box-shadow: 0 0 10px 3px rgba(51, 51, 51, .8);
  width: 100px;
  height: 100px;
  margin: -50px 0 0 -50px;
}

.youtube::after { /* Play Triangle */
  content: '';
  width: 0;
  height: 0;
  line-height:0px;
  display:block;
  border-style: solid;
  border-width: 20px 0 20px 40px;
  margin: -20px 0 0 -15px;
  border-color: transparent transparent transparent #333;
  position: absolute;
  top: 50%;
  left: 50%;
  height: 0;
  width: 0;
  z-index:2;
} 
.youtube iframe{ /* youtube embed (responsive) */
  width:100%;
  height:100%;
  position:absolute;
  top:0;
  left:0;
  z-index:3;
}