
.modal-editor { text-align: center; }

.modal-editor-inner {
	display: inline-block;
	background: #fff;
	margin: 20px auto;
	padding: 20px 30px;
	max-width: 860px;
	text-align: left;
}
.modal-field-label { color: #666; font-size: 12px; margin: 20px 0 5px; }

.invalid { border-color: $brand !important; background-color: $brandLighter !important; color: black !important; }

.modal-title-row {
	color: #666; 
	padding-bottom: 15px; 
	text-transform: uppercase; 
	font-size: 20px; 
	&:after {
		content: " "; 
		display: block; 
		padding-bottom: 15px; 
		border-bottom: 1px solid $greyLight;
	}
}

.modal-button-row { 
	padding-top: 15px; 
	&:before {
		content: " "; 
		display: block; 
		padding-top: 15px; 
		border-top: 1px solid $greyLight;
	}
}

.modal-action { color: #999; text-transform: uppercase; font-size: 20px; text-decoration: none; 
	&:hover { color:#666; }
}

.modal-error {
	opacity: 0;
	color: #f00;
	font-size: 12px;
	font-weight: bold;
	float: right;
	transition: opacity 0.3s;

	&.is-active { opacity: 1; }
}
