
.modal-editor-inner.ministry-editor {
	display: block;
}

.ministry-preview {
    height: 200px;
    background-size: cover !important;
    background-repeat: no-repeat !important;
    background-position: center !important;
    background-color: #666;
    border: 1px solid #666;
    color: $white;
    overflow: hidden;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
	&::before {
		content: " ";
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: linear-gradient(to bottom, rgba(0,0,0,.1) 20%,rgba(0,0,0,0.7) 100%);
	}
	.ministry-preview-title {
		font-size: 14px;
		font-weight: bold;
		position: absolute;
		left: 0;
		right: 0;
		bottom: 20px;
	}
}

.ministry-thumbs.row {
	margin: 5px -5px 0;
	max-height: 140px;
	overflow: auto;
	&::-webkit-scrollbar {
		width: 12px;
	}
	&::-webkit-scrollbar-track {
		background-color: #f0f0f0;
		border-left: none;
	}
	&::-webkit-scrollbar-thumb {
		background-color: #ccc;
	}
	&::-webkit-scrollbar-thumb:hover {
		background-color: #aaa;
	}
	>div {
		position: relative;
		float: left;
		width: 31%;
		height: 46px;
		margin: 5px;
		cursor: pointer;
		&.ministry-imgThumb {
			>div { position: absolute; top: 0; right: 0; bottom: 0; left: 0; background-size: cover !important; background-repeat: no-repeat !important; background-position: center !important; z-index: 2; }
			&:after {
				content: "\f110";

				display: inline-block;
				font: normal normal normal 14px/1 FontAwesome;
				font-size: inherit;
				text-rendering: auto;
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;

				font-size: 2em;

				-webkit-animation: fa-spin 2s infinite linear;
				animation: fa-spin 2s infinite linear;

				position: absolute;
				left: 50%;
				top: 50%;
				margin-top: -0.5em;
				margin-left: -0.5em;
				z-index: 1;
			}		
		}
		&:hover { box-shadow: 0 0 2px 0px $blue; }
		&.selected { box-shadow: 0 0 2px 1px $blue; }
	}
}

/* admin section */
.admin--ministryTiles { margin-bottom: 20px; }
.admin--ministryTile {
	height: 180px;
	position: relative;
	user-select: none;
	>div {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		cursor: move;
		* {
			cursor: move !important;
		}
	}
	>span {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		line-height: 180px;
		font-size: 48px;
		text-align: center;
		color: $white;
		z-index: 3;
		opacity: 1;
		transition: opacity 0.3s;
		cursor: move;
		text-shadow: 0px 1px 2px #000;
		background: rgba(0, 0, 0, 0.2);
		&:hover {
			opacity: 0;
		}
	}
	&:hover span {
		opacity: 0;
	}
	.ministry-preview {
		margin: 0;
		border: none;
		padding: 20px;
		height: auto;
        .ministry-preview-title { font-size: 13px; bottom: 18px; }
        i.fa { position: absolute; top: 20px; right: 20px; color: #ccc; opacity: 0.75; font-size: 24px; }
	}
}

.admin--ministryDetails {
	.admin--tree-page {
		min-height: 71px;
		.ministryDetails-img {
			background-size: cover!important;
			background-repeat: no-repeat!important;
			background-position: center!important;
			background-color: #666;

			float: left;
			width: 75px;
			height: 40px;
			margin: 0 15px;
			overflow: hidden;
			position: relative;
		}
		.ministryDetails-title {
			font-weight: bold;
			color: $black;
			line-height: 40px;
		}
		&:after {
			content: " ";
			clear: both;
		}
	}
}