@import "shared/variables";
@import "vendor/bootstrap/grid";
@import "vendor/dropzone";
@import "vendor/balloon";

@import "shared/globals";
@import "shared/padding";
@import "shared/margin";
@import "shared/btn";
@import "shared/utilities";
@import "shared/inputs";

@import "admin/modal";
@import "admin/slide-editor";
@import "admin/ministry-editor";
@import "admin/giving-toolbar";
@import "admin/our-leader";
@import "admin/contact-info";
@import "admin/worship";
@import "admin/media-box";

* {
	box-sizing:border-box;
}

@keyframes rotate{
	from { transform: rotate(0deg) translate3d(0,0,0); }
	to { transform: rotate(359deg) translate3d(0,0,0); }
}

@keyframes opacity {
	from { opacity:0; }
	to { opacity: 1; }
}

html, body { height: 100%; }

body {
	font-family: 'Open Sans', sans-serif;
	font-size:14px;
	color:#111;
	width:100%;
	overflow-x:hidden;
	margin:0;
}

body, div {
	&.loading:before {
		display: block;
		content: "";
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		position: fixed;
		z-index: 9991;
		background: rgba(255,255,255,.8);
	}
	&.loading:after {
		width: 50px;
		height: 50px;
		position: fixed;
		top: calc(50% - 30px);
		left: calc(50% - 30px);
		display: block;
		content: "";
		z-index: 9992;
		border-top: 6px solid #C01D2B;
		border-right: 6px solid #e51d2e;
		border-bottom: 6px solid #e51d2e;
		border-left: 6px solid #e51d2e;
		border-radius: 100%;
		animation: rotate 1s infinite linear, opacity 1s forwards;
		opacity: 0;
		padding: 0;
	}
}

.text-right {
	text-align: right;
}

.text-left {
	text-align:left;
}

.text-center {
	text-align:center;
}

.hidden {
	visibility:hidden;
	display:none;
}

.inline-block {
	vertical-align: middle;
	display: inline-block;
}

.active {
	opacity:1;
	transition:all .35s;
}

.inactive {
	opacity:.4;
}

.admin--wrapper {
	position:relative;
	z-index:3;
	transform:translate3d(0,0,0);
	transition:transform .35s;
	margin-left:40px;
	height: 100%;
}

.admin--header {
	position:fixed;
	z-index:4;
	width:100%;
	padding: 10px 0;
	font-size:13px;
	background:white;
}

.admin--header-title {
	font-size:24px;
}

.admin--header-subtitle {
	font-size:18px;
}

.admin--header-trigger {
	text-transform:uppercase;
	font-size:13px;
	text-align:center;
	padding:5px 0;
	display:block;
	float:left;
	cursor:pointer;
	z-index:20;
	position: relative;
	.fa {
		font-size:18px;
		float:left;
		margin:-1px 5px 0 0;
	}
}

.admin--navigation-items {
	list-style-type:none;
	padding:0;
	margin:0;
	position:fixed;
	left:0;
	top:0;
	width:40px;
	padding-top:10px;
	height:100%;
	background:black;
	z-index:40;
	transition:width .15s;
	overflow:hidden;
	.admin--navigation-label {
		opacity:0;
		overflow:hidden;
		transition:all .15s;
		display:block;
		font-size:12px;
	}
	&.expanded {
		width:300px;
		z-index:100;
		.admin--navigation-label {
			opacity:1;
			transition:all 1s;
		}
	}
	.fa {
		margin-right:20px;
		float:left;
		font-size: 17px;
		width: 17px;
		text-align: center;
	}
}

.admin--navigation-item {
	width:300px;
	a {
		display:block;
		color:white;
		text-decoration:none;
		padding:10px;
		overflow: hidden;
		&:hover {
			background-color:rgba(255,255,255,.2);
		}
	}
	&.active {
		a {
			color:$brand;
			background:white;
			transition:all 0s;
			&:hover {
				color:white;
				background:$brand;
				.fa {
					color:white;
				}
			}
			.fa {
				color:$brand;
			}
		}
	}
}

.admin--message {
	color: #999;
	font-size: 13px;
	padding: 1em 0 .5em;
	line-height: 1.4;
}

.admin--countdown {
	position: fixed;
	z-index: 90;
	text-align: center;
	width: 100%;
	top: 60px;
	left:40px;
	color: white;
	background: $brand;
	padding: 1.75em 0;
	font-weight: bold;
	transform:translate3d(0,-200px,0);
	transition:all .35s;
	opacity:0;
	&.reveal {
		transform:translate3d(0,0,0);
		opacity:1;
	}
}

.admin--tree-wrapper {
	margin:0 0 1em 0;
}

.admin--tree-heading {
	font-size: 20px;
	font-weight: 400;
	background:#e6e6e6;
	color:#666;
	padding:0 0 0 20px;
	margin:0;
    height: 55px;
    line-height: 55px;
    .btn.btn-new {
        padding: 0 20px;
        border: none;
        margin: 0;
        border-radius: 0;
        font-size: 14px;
        font-weight: bold;
        background: $green;
        color: #fff;
        &:hover {
            background: $greenDark;
        }
		.fa-plus {
			border: 2px solid #fff;
			float: left;
			margin-right: 8px;
			font-size: 10px;
			line-height: 18px;
			height: 20px;
			width: 28px;
			text-align: center;
			margin-top: 17.5px;
            position: relative;
            &.page {
                line-height: 24px;
                height: 24px;
                top: -2px;
                &:after {
                    content: " ";
                    position: absolute;
                    top: 0;
                    right: 0;
                    left: 0;
                    height: 4px;
                    border-bottom: 1px solid white;
                }
            }
		}
    }
}

.admin--tree-pages {
	margin:0;
	padding:0;
	list-style-type:none;
}

.admin--tree-page {
	border-bottom:1px solid #e6e6e6;
	transition:all .35s;
	padding:0 10px;
	> a {
		display: block;
		position:relative;
		padding:10px;
		&:visited {
			color:black;
		}
	}
	&:hover {
		background: #d9d9d9;
		transition: all .35s;
		input[type="text"],
		input[type="email"],
		input[type="url"] {
			background:#f7f7f7;
		}
	}
}

.admin--page-title {
	color:#4d4d4d;
	font-weight:bold;
	margin:0;
	font-size:16px;
}

.admin--page-update {
	color:#0071bc;
	margin:0;
	font-size:12px;
}

.admin--icon-event {
	float: left;
	margin: 5px 15px 0 0;
	width: 30px;
	height: auto;
}

.admin--editor-page-title {
	background: $offWhite;
	height: 55px;
	line-height: 55px;
	* {
		margin-top: 0;
		margin-bottom: 0;
	}
	.btn {
		height: 55px;
		line-height: 55px;
		background: $green;
		color: $white;
		border: none;
		font-size: 14px;
		padding: 0 20px;
		border-radius: 0;
		transition: background-color 0.3s;
		&:hover {
			background: $greenDark;
		}
		.fa-plus {
			border: 2px solid #fff;
			float: left;
			margin-right: 8px;
			font-size: 10px;
			line-height: 18px;
			height: 20px;
			width: 28px;
			text-align: center;
			margin-top: 17.5px;
		}
	}
}

[class^=admin--icon] {
	vertical-align: middle;
	display:inline-block;
	font-size:22px;
	margin:5px 10px 0 0;
	cursor:pointer;
	transition:all .35s;
	opacity:.75;
	color: $black;
	&:hover {
		opacity:1;
	}
    &.inactive {
        cursor: default;
        opacity: .25 !important;
    }
	.admin--editor-page-title & {
		width: 55px;
		height: 55px;
		text-align: center;
		line-height: 55px;
		margin:0;
	}
}

.admin--page-title-icons {
	width:300px;
	margin:0 0 0 10px;
	[class^=admin--icon] {
		margin: 0 -4px 0 0;
		opacity: 1;
		&:hover,
		&.active {
			background:$grey;
			color:white;
		}
	}
}

.admin--preview-window-inner {
	max-width:100%;
	transition:max-width .35s;
	margin:0 auto;
	display:block;
	float:none;
	border:10px solid transparent;
	border-radius:5px;
	&.tablet {
		max-width:768px;
		border-color:$grey;
	}
	&.mobile {
		max-width:480px;
		border-color:$grey;
	}
	iframe {
		width:100%;
		height:1600px;
		border:none;
		outline:none;
	}
}

.admin--icon-external {
	background:$blueDark;
	color:white;
}

.admin--icon-toggle {
	width:44px;
	height:22px;
	border-radius:20px;
	background:#8fd894;
	border:2px solid #4d4d4d;
	position:relative;
	&:after {
		background: #e6e6e6;
		border-radius: 50%;
		width: 18px;
		height: 18px;
		position: absolute;
		right:0;
		top:0;
		content: "";
		border: 2px solid #4d4d4d;
		transform:translate(2px,-2px);
		transition:all .35s;
		font:normal normal normal 14px/1 FontAwesome;
	}
	&.unpublished {
		background:#e08585;
		&:after {
			transform:translate(-22px,-2px);
		}
	}
	&.locked {
		&:after {
			content:"\f023";
			text-align: center;
			line-height: 19px;
			color: #999;
		}
	}
}

.admin--shield {
	max-height: 40px;
	width:auto;
	float:left;
	margin:0 10px 0 46px;
}

.admin--site-title {
	margin:0;
	font-size:18px;
	line-height:35px;
}

.admin--username {
	line-height: 41px;
	font-size: 11px;
}

.admin--editor-sections {
	padding: 0;
	background: $offWhite;
	position: absolute;
	top: 21px;
	left: 0;
	bottom: 0;
    overflow: auto;
	div {
		display: block;
		position: relative;
		min-height: 55px;
		line-height: 55px;
		padding: 0 46px 0 56px;
		font-size: 20px;
		transition: all .3s;
		&.is-header {
			background: #e6e6e6;
			padding: 0 20px;
			select {
				font-size: 20px;
			}
		}
		&:not(.is-header){
			&:hover {
				background: #f7f7f7;
				cursor: pointer;
			}
			&.is-selected {
				background: #f7f7f7;
				.fa-angle-right {
					opacity: 1;
				}
			}
			&.is-child:before {
				content: "\2027";
				margin-right: 15px;
				font-size: 54px;
				line-height: 55px;
				height: 55px;
				position: absolute;
				left: 20px;
				top: 0;
			}
			&:not(.is-child):before {
				content: "[]";
				margin-right: 15px;
				font-size: 26px;
				line-height: 55px;
				height: 55px;
				position: absolute;
				left: 20px;
				top: 0;
			}
			.fa-angle-right {
				position: absolute;
				right: 20px;
				top: 50%;
				transform: translate(0,-50%);
				line-height: 55px;
				opacity: 0;
				transition: opacity .3s;
			}
		}
	}
}
@media (max-width: $screen-sm-min) {
	.admin--editor-sections {
		position: relative;
		left: auto;
		bottom: auto;
	}
}

.admin--editor-container {
    > div.has-SaveCancel {
        padding-bottom: 75px;
        .modal-button-row {
            position: absolute;
            bottom: 15px;
            left: 26%;
            right: 15px;
        }
    }
}
@media (max-width:$screen-sm-min) {
    .admin--editor-container {
        > div.has-SaveCancel {
            padding-bottom: 0;
            .modal-button-row {
                position: relative;
                bottom: auto;
                left: auto;
                right: auto;
            }
        }
    }
}

.collapsible {
	transition:all .35s;
	overflow:hidden;
	max-height:0;
	&.opened {
		max-height:9999px;
	}
}

.collapsible-trigger {
	cursor:pointer;
}

.mfp-popup {
	width:80%;
	max-width:400px;
	background: white;
	margin: 0 auto;
	border-radius: 10px;
	padding: 1em;
	position: relative;
	h1,h2,h3,h4,h5 {
		margin-top:0;
	}
}

.admin--toggled-visibility {
	transition:all .35s;
	opacity:0;
	overflow:hidden;
	max-height:0;
	&.visible {
		max-height:9999px;
		opacity:1;
	}
}

[data-view=socialMedia] {
	margin:0;
	p {
		margin:0 0 5px 0;
	}
	.fa {
		font-size: 16px;
		color: white;
		background:$brand;
		height:30px;
		width: 34px;
		line-height:1.9;
		border-radius:50%;
		margin: 0 10px 0 0;
		text-align: center;
		&.fa-twitter {
			background:$twitter;
		}
		&.fa-facebook {
			background:$facebook;
		}
		&.fa-instagram {
			background:$instagram;
		}
		&.fa-vine {
			background:$vine;
		}
		&.fa-pinterest {
			background:$pinterest;
		}
	}
}
.admin--legal {
	font-size:12px;
	text-transform:uppercase;
	color:#ccc;
	margin:2em 0;
	display: block;
	text-align: center;
	line-height: 1.2;
}

.admin--error-text {
	position: absolute;
	right: 0;
	width: auto;
	top: 0;
	text-align: right;
	line-height: 37px;
	background: red;
	color: white;
	padding: 0 10px;
	font-weight: bold;
	font-size: 12px;
	max-width:999px;
	overflow:hidden;
	transition:all .5s;
	transform:translate(0,0);
	&.inactive {
		transform:translate(100%,0);
	}
}

/* Overrides */

#contentDiv {
	background:#f7f7f7;
	padding:21px 0 10px 0;
}

#thumbnail, #thumbFacebookMetaTag {
	width: calc(100% - 110px);
}

table#details_table table, .x-form-item {
	width: 100% !important;
	border: none;
}

table#details_table {
	tr {
		td {
			border-bottom:none;
			&:last-child {
				padding-right: 0;
				padding-left: 0;
			}
		}
	}
}

.question-btn { 
	background: #ccc; 
	color: #fff; 
	width: 16px; 
	height: 16px; 
	display: inline-block; 
	text-align: center; 
	line-height: 16px; 
	font-size: 12px;
	cursor: pointer; 
	&:hover {
		background: #aaa;
	}
}