@mixin colors($variable) {
	border-color:$variable;
	color:$variable;
	@if $variable == $white {
		background-color:transparent;
	} @else if $variable == transparent {
		background-color:transparent;
		color:$brand;
		border-color:$brand;
	}
	&:hover {
		background-color:$variable;
		color:white;
		@if $variable == $white {
			color:$blueDark;
		} @else if $variable == transparent {
			background-color:$brand;
			color:$white;
			border-color:$brand;
		}
		border-color:$variable;
	}
	&.btn-solid {
		background-color:$variable;
		color:white;
		@if $variable == $greyLighter {
			color: #919191;
		}
	}
	&.btn-solid:hover {
		color:$variable;
		background-color:white;
		@if $variable == $greyLighter {
			color: white;
			background-color:$brand;
		}
	}
	+ .btn-solid:not(.btn-red) {
		background-color:lighten($variable,20);
		border-color:lighten($variable,20);
	}
	&.btn-inverse {
		background-color:transparent;
		border-color:$variable;
		color:$variable;
	}
	&.btn-inverse:hover {
		.no-touchevents & {
			background-color:white;
			color:$variable;
			@if $variable == $white {
				color:$blueDark;
			}
		}
	}
}

.btn {
	padding: 15px 35px;
	border: 2px solid $greyLight;
	border-radius: 5px;
	background: white;
	color: $greyDark;
	font-size: 16px;
	display: inline-block;
	vertical-align: middle;
	cursor: pointer;
	text-decoration:none;
	transition: all .35s;
	margin:.25em 0;
	text-align: center;
	.fa {
		margin-right: 10px;
	}
	&:hover {
		.no-touchevents & {
			color:white;
			background:$greyLight;
			border-color:$greyLight;
		}
	}
	&.btn-block {
		display:block;
		clear: both;
		max-width:100%;
	}
	&.btn-md {
		font-size:20px;
	}
	&.btn-lg {
		font-size:22px;
	}
	&.btn-caps {
		text-transform:uppercase;
	}
	&.btn-new {
		background:white;
		color:$greyDark;
		&:hover {
			background:$greyDark;
			color:white;
		}
	}
	&.btn-save {
		background:$vine;
		color:white;
		padding: 5px 40px;
		font-weight: bold;
		border-color: $vine;
		&:hover {
			background:white;
			color:$vine;
		}
		&.inactive {
			opacity:.4;
			cursor:default;
			background:#ccc;
			padding:5px 10px;
			&:hover {
				background:$vine;
				color:white;
			}
		}
	}
	&.btn-delete {
		background:none;
		color:$brand;
	}
}

// Remove the margin if the button is displayed inline with other elements
.form-group .btn {
	margin:0;
}

.btn.btn-black { @include colors($black) }
.btn.btn-blue { @include colors($blue) }
.btn.btn-red { @include colors($brand) }
.btn.btn-grey { @include colors($grey) }
.btn.btn-greyLighter { @include colors($greyLighter) }
.btn.btn-white { @include colors($white) }
.btn.btn-transparent { @include colors(transparent) }

input[type="button"], .button {
	display: inline-block;
	padding: 10px 15px;
	background: black;
	border: none;
	color: white;
	font-weight: bold;
	cursor: pointer;
	text-decoration:none;
}

@media (min-width:$screen-sm-min) {
	.btn {
		padding-top:5px;
		padding-bottom:5px;
		font-size:18px;
	}
	.btn.btn-inverse {
		background:transparent;
		border-color:white;
		color:white;
	}

	.btn.btn-inverse:hover {
		.no-touchevents & {
			background:white;
			color:$brand;
		}
	}
}